import Router from '@/components/Setting/router'
import Index from '@/components/Setting/index'
import Advert from '@/components/Setting/advert'
import MenuAdvert from '@/components/Setting/menuAdvert'
import Strategy from '@/components/Setting/strategy'
import Question from '@/components/Setting/question'
import Site from '@/components/Setting/site'
import MenuLogos from '@/components/Setting/menuLogos'

export default {
  path: '/setting',
  name: '',
  icon: 'el-icon-setting',
  component: Index,
  hidden_child: false,
  children: [
    {
      name: '',
      path: 'manager',
      component: Router,
      children: [
        {path: 'advert', component: Advert, name: '', meta: {title: ""}},
        {path: 'menuAdvert', component: MenuAdvert, name: '', meta: {title: ""}},
        {path: 'strategy', component: Strategy, name: '', meta: {title: ""}},
        {path: 'question', component: Question, name: '', meta: {title: ""}},
        {path: 'site', component: Site, name: '', meta: {title: ""}},
        {path: 'menuLogos', component: MenuLogos, name: '', meta: {title: ""}},
      ],
      redirect: '/setting/manager/advert'
    },
  ],
  redirect: '/setting/manager'
}



