import Router from '@/components/Energy/router'
import Index from '@/components/Energy/index'
import Poster from '@/components/Energy/poster'
import PosterAdd from '@/components/Energy/Add'
import Video from '@/components/Energy/video'
import Picture from '@/components/Energy/picture'
import Category from '@/components/Energy/category'
import HotTag from '@/components/Energy/tag'
import ListAdvert from '@/components/Energy/advert'
import Quotes from '@/components/Energy/quotes'

export default {
  path: '/energy',
  name: '',
  icon: 'el-icon-news',
  component: Index,
  hidden_child: false,
  children: [
    {
      name: '',
      path: 'manager',
      component: Router,
      children: [
        {
          name: '',
          path: 'poster',
          component: Poster,
          meta: {
            title: ""
          }
        },
        {
          name: '',
          path: 'video',
          component: Video,
          meta: {
            title: ""
          }
        },
        {
          exclude: true,
          path: ':type/add',
          component: PosterAdd,
          meta: {
            title: ''
          },
        },
        {
          exclude: true,
          path: ':type/edit/:id',
          component: PosterAdd,
          meta: {
            title: ''
          },
        },
        {
          name: '',
          path: 'quotes',
          component: Quotes,
          meta: {
            title: ""
          }
        },
        {
          name: '',
          path: 'picture',
          component: Picture,
          meta: {
            title: ""
          }
        },
        {
          name: '',
          path: 'category',
          component: Category,
          meta: {
            title: ""
          }
        },
        {
          name: '',
          path: 'advert',
          component: ListAdvert,
          meta: {
            title: ""
          }
        },
        {
          name: '',
          path: 'tag',
          component: HotTag,
          meta: {
            title: ""
          }
        },
      ],
      redirect: '/energy/manager/poster'
    },
  ],
  redirect: '/energy/manager'
}



