
import http from '@/utils/http'


export default {

  // 
  getStrategyList : params => {
      params.type = 1
      return http.post("/docs/list", params)
  },

  // 
  getQuestionList : params => {
      params.type = 2
      return http.post("/docs/list", params)
  },

  // 
  saveStrategy : params => {
      params.type = 1
      return http.post("/docs/save", params)
  },

  // 
  saveQuestion : params => {
    params.type = 2
    return http.post("/docs/save", params)
  },

  // 
  batchDelete : ids => {
      return http.post("/docs/delete", ids)
  },

  // 
  batchPublish : (ids, flag) => {
      return http.post("/docs/publish/" + flag, ids)
  },

  /**
   * 
   */
  uploadPic: pics => {
    return http.post("/files/upload", pics);
  }
}



