<template>
  <div class="member-list">
    <common-template>
      <h1 class="content__title" slot="title">{{$route.meta.title}}</h1>

      <div slot="header" class="content__main-operation">
        <div class="operation-left">
          <el-button type="primary" size="small" @click="handleAdd"></el-button>
          <el-button size="small" plain @click="handleDelete(0)"></el-button>
        </div>
      </div>

      <div class="content__main-list" slot="list">
        <el-table
          v-loading = "!list"
          @selection-change="handleSelectionChange"
          ref="multipleTable"
          :data="list"
          tooltip-effect="dark"
          style="width: 100%">

          <el-table-column
            type="selection"
            width="55">
          </el-table-column>

          <el-table-column
            label=""
            align="left"
            prop="title">
          </el-table-column>
          <!--<el-table-column-->
            <!--label=""-->
            <!--align="left"-->
            <!--prop="brief">-->
          <!--</el-table-column>-->

          <el-table-column
            label=""
            width="80"
            align="center"
            prop="sort">
          </el-table-column>
          <el-table-column
            width="150"
            label=""
            align="center">
            <template slot-scope="scope">
              <el-switch
                v-model="scope.row.frozen"
                :active-text="scope.row.frozen ? '' : ''"
                @change = "changeStatus(scope.row.id, scope.row.frozen)">
              </el-switch>
            </template>
          </el-table-column>

          <el-table-column
            width="200"
            label=""
            align="center"
            prop="createTime">
          </el-table-column>

          <el-table-column
            label=""
            width="150"
            align="center">
            <template slot-scope="scope">
              <el-button
                size="mini"
                @click="handleEdit(scope.$index)"></el-button>
              <el-button
                type="danger"
                size="mini"
                @click="handleDelete(scope.row.id)"></el-button>
            </template>
          </el-table-column>
        </el-table>

        <div style="text-align: center; margin-top: 60px;" v-if="list && list.length>0">
          <el-pagination
            :page-sizes="[10, 20, 30, 40, 50]"
            :page-size="page.pageSize"
            :current-page="page.pageNo"
            layout="total, sizes, prev, pager, next, jumper"
            :total="page.totalRows"
            @size-change = "changePageSize"
            @current-change = "changePageNo">
          </el-pagination>
        </div>

      </div>
    </common-template>

    <!--  -->
    <el-dialog title="" :visible.sync="editVisible">
      <el-form :model="docs" ref="EditForm" :rules="rules" label-width="80px">
        <el-form-item label="" prop="title">
          <el-input v-model.trim="docs.title" auto-complete="off" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="brief">
          <vue-html5-editor ref="editor" :content="docs.content" :show-module-name="false" :height="300" @change="updateData"></vue-html5-editor>
          <!--<el-input v-model.trim="docs.brief" auto-complete="off" placeholder=""></el-input>-->
        </el-form-item>

        <el-form-item label="" prop="sort">
          <el-input size="small" v-model="docs.sort"></el-input>
          <p class="xs-assist-label"></p>
        </el-form-item>

        <el-form-item label="" prop="frozen">
          <el-switch v-model="docs.frozen" :active-text="docs.frozen ? '' : ''"></el-switch>
        </el-form-item>
      </el-form>

      <div slot="footer" class="dialog-footer">
        <el-button @click="editVisible = false" size="mini"> </el-button>
        <el-button type="primary" @click="save()" :loading="loading" size="mini"> </el-button>
      </div>
    </el-dialog>

    <!--  -->
    <upload-photo
      :limit="5"
      :limit_max="false"
      :show="isShowUpload"
      :current_len="editor_imgs.length"
      @confirm="confirmEditorImg"
      @update:show="switchEditorShow"
    ></upload-photo>

  </div>

</template>

<script>
  import { mapGetters, mapMutations } from 'vuex';
  import CommonTemplate from '@/components/common-template'
  import UploadPhoto from 'base/upload-photo/upload-photo.vue';
  import DocApi from '../../api/docs';

  export default {
    name: 'docs-list',
    components: { CommonTemplate, UploadPhoto },
    computed: {
      ...mapGetters([
        'isShowUpload',
        // ...
      ])
    },
    data() {
      return {
        editor_imgs: [],

        loading: false,
        editVisible: false,
        docs: { frozen: true, sort: 100},
        list: null,
        page: {pageNo:1, pageSize: 20},
        choose_id_arr: [],
        rules:{
          title: [
            { required: true, message: '', trigger: 'blur' },
          ],
          content: [
            { required: true, message: '', trigger: 'blur' },
          ],
          sort: [
            { required: true, message: '', trigger: 'blur' },
          ],
        }
      }
    },
    created() {
      this.getList()
    },
    methods: {
      getList() {
        let req = {
          p: this.page.pageNo,
          s: this.page.pageSize,
        }
        DocApi.getQuestionList(req).then((res) => {
          this.list = res.list
          this.list.forEach(item => {
            if (!item.content) {
              item.content = item.brief
            }
            item.frozen = Boolean(item.frozen)
          })
          this.page = res.page
        }, (err) => {
          this.$message.error(err.msg);
        })
      },

      save() {
        this.$refs.EditForm.validate((valid) => {
          if (valid) {
            this.loading = true
            DocApi.saveQuestion(this.docs).then((res) => {
              this.loading = false
              if (res.code === 0) {
                this.editVisible = false
                this.$message.success('');
                this.reload()
              } else {
                this.$message.error(res.msg);
              }
            }, (err) => {
              this.loading = false
              this.$message.error(err.msg);
            })
          }
        });
      },

      // 
      updateData(content) {
        this.docs.content = content
      },

      reload() {
        this.list = null
        this.page.pageNo = 1
        this.getList()
      },

      handleSelectionChange(val) {
        this.choose_id_arr = val
      },
      toggleSelection(rows) {
        if (rows) {
          rows.forEach(row => {
            this.$refs.multipleTable.toggleRowSelection(row);
          });
        } else {
          this.$refs.multipleTable.clearSelection();
        }
        this.choose_id_arr = rows
      },

      changePageSize(value){ // size
        this.page.pageNo = 1
        this.page.pageSize = value
        this.list = null
        this.getList()
      },
      changePageNo(value){ // currentPage
        this.page.pageNo = value
        this.list = null
        this.getList()
      },

      handleAdd(){
        this.editVisible = true
        this.docs = { frozen: true, sort: 100 }
      },
      handleEdit(idx){
        this.editVisible = true
        this.docs = this.list[idx]
      },

      ...mapMutations([
        'showUpload'
      ]),
      confirmEditorImg(imgs) {
        this.showUpload(false)
        console.log(imgs)
        const loading = this.$loading({
          lock: true,
          text: 'Loading',
          spinner: 'el-icon-loading',
          background: 'rgba(0, 0, 0, 0.7)'
        });
        // 
        DocApi.uploadPic(imgs).then((res) => {
          loading.close()
          if (res.picArr) {
            res.picArr.forEach(pic => {
              this.$refs.editor.execCommand("insertImage", pic)
            })
          }
        }, (err) => {
          loading.close()
          this.$message.error(err.msg);
        })
      },
      switchEditorShow(val) {
        this.showUpload(val)
      },

      // 
      changeStatus(id, flag) {
        DocApi.batchPublish([id], !flag?0:1).then((res) => {
          this.$message.success('');
          this.reload()
        }, (err) => {
          this.$message.error(err.msg);
        })
      },

      // 
      handleDelete(id) {
        let ids = [id]
        if (id == 0) { // 
          if (this.choose_id_arr.length === 0) {
            this.$message.error("");
            return
          }
          let ids = this.choose_id_arr.map(item => item.id)
        }

        this.$confirm('', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          DocApi.batchDelete(ids).then((res) => {
            this.$message.success('');
            this.reload()
          }, (err) => {
            this.$message.error(err.msg);
          })
        }).catch(() => {
        });
      }
    }
  }
</script>

<style lang="scss">
  .member-list {

  }
</style>



