
import http from '@/utils/http'


export default {

  // 
  getList : params => {
    return http.post("/quotes/list", params)
  },
  // 
  save : params => {
    return http.post("/quotes/save", params)
  },

  // 
  batchDelete : ids => {
      return http.post("/quotes/delete", ids)
  },

  // 
  batchPublish : (ids, flag) => {
    return http.post("/quotes/publish/" + flag, ids)
  },
}



