import Router from '@/components/Member/router'
import Index from '@/components/Member/index'
import List from '@/components/Member/list'

export default {
  path: '/member',
  name: '',
  icon: 'el-icon-view',
  component: Index,
  hidden_child: false,
  children: [
    {
      name: '',
      path: 'manager',
      component: Router,
      children: [
        {
          name: '',
          path: 'list',
          component: List,
          meta: {
            title: ""
          }
        },
      ],
      redirect: '/member/manager/list'
    },
  ],
  redirect: '/member/manager'
}



