import Router from '@/components/Store/router'
import Index from '@/components/Store/index'
import Goods from '@/components/Store/goods'
import GoodsAdd from '@/components/Store/add'
import CentGoods from '@/components/Store/cent'
import CentAdd from '@/components/Store/cent_add'
import Orders from '@/components/Store/orders'

export default {
  path: '/store',
  name: '',
  icon: 'el-icon-star-off',
  component: Index,
  hidden_child: false,
  children: [
    {
      name: '',
      path: 'manager',
      component: Router,
      children: [
        {
          name: '',
          path: 'goods',
          component: Goods,
          meta: {
            title: ""
          }
        },
        {
          exclude: true,
          path: 'goods/add',
          component: GoodsAdd,
          meta: {
            title: ''
          },
        },
        {
          exclude: true,
          path: 'goods/edit/:id',
          component: GoodsAdd,
          meta: {
            title: ''
          },
        },
        {
          name: '',
          path: 'cent',
          component: CentGoods,
          meta: {
            title: ""
          }
        },
        {
          exclude: true,
          path: 'cent/add',
          component: CentAdd,
          meta: {
            title: ''
          },
        },
        {
          exclude: true,
          path: 'cent/edit/:id',
          component: CentAdd,
          meta: {
            title: ''
          },
        },
        {
          name: '',
          path: 'orders',
          component: Orders,
          meta: {
            title: ""
          }
        },
      ],
      redirect: '/store/manager/goods'
    },
  ],
  redirect: '/store/manager'
}



